import React, { Component } from 'react'

export default function Login (props) {
    function toLogin(){
        props.history.push('./home')
    }
    var style={float:'left',margin:200}
    
    return (
        <div  style={style} >
            用户名<input type='text' name='username' />
            <br />
            <br />
            密码  <input style={{marginLeft:10}} type='password' name='password' />
            <br />
            <br />
            <button style={{marginLeft:50,width:100}} type='submit' value='submit' onClick={toLogin} > 登录</button>
        </div>
    )
    
}
