import React, { Component } from 'react'
import {Link,Route,Router} from 'react-router-dom'
import All from './container/home/All'
import Good from './container/home/Good'
import Ask from './container/home/Ask'
import Share from './container/home/Share'
import Sider from './container/home/Sider'
import Job from './container/home/Job'

export default class Home extends Component {
    render() {
        let {url} = this.props.match;
        let style = {marginLeft:20};
        return (
            <div>
                <div style={style}>
                    <Link style={style} to={`${url}/all/:id`}>全部</Link>
                    <Link style={style} to={`${url}/good/:id`}>精华</Link>
                    <Link style={style} to={`${url}/share/:id`}>分享</Link>
                    <Link style={style} to={`${url}/ask/:id`}>问答</Link>
                    <Link style={style} to={`${url}/job/:id`}>招聘</Link>
                </div>  
                <div>
                    {/* <Route path={url+'/all'} component={All} /> */}
                    <Route path={url+'/all/:id'} component={All} />
                    <Route path={url+'/ask/:id'} component={Ask} />

                    <Route path={url+'/share/:id'} component={Share} />
                    <Route path={url+'/good/:id'} component={Good} />
                    <Route path={url+'/job/:id'} component={Job} />
                </div>
                {/* <Sider/> */}

            </div>
        )
    }
}
