import {NavLink} from 'react-router-dom';
import React, { Component } from 'react'

// import renderEmpty from 'antd/lib/config-provider/renderEmpty';
// import {useHistory} from 'react-router-dom';

// let url = 'https://cnodejs.org/api/v1';

export default class Content extends Component {
    constructor(){
        super();
        this.state = {
            data: [],
            e:''
        }
        
    }
    componentDidMount(){
        let page = this.props.match.params.id;
        let e = this.props.location.pathname.slice(9);
        console.log(e);
        
        fetch('https://cnodejs.org/api/v1/topics?page='+page)
            .then((res)=>res.json())
            .then((res)=>{
                console.log(res)
                
                this.setState({
                    data: res.data,
                    e:e
                });
            })
    }
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.id!==this.props.match.params.id){
            let page = this.props.match.params.id;
            console.log(page);
            fetch('https://cnodejs.org/api/v1/topics?page='+page)
                .then((res)=>res.json())
                .then((res)=>{
                    console.log(res)
                    this.setState({
                        data: res.data
                    });
                })
        }
    }
    
    render(){
        let style = {float: 'left',border:'0.1px solid green',width:20,height:30,textAlign:'center',padding:3,listStyle:'none'}
        console.log(this.state.e);
        return (
            <div>
                {
                this.state.data.map((item)=>{
                    console.log(this.state.e===item.id);

                    if(this.state.e===item.id){
                        return(
                            <div style={{marginLeft:40}}>
                                { 
                                    <div style={{background:'white',border:'0.2px solid green',width:850,overflow:'hidden',color:'black'}} key={item.id}>
                                        <img style={{height:30,width:30}} src={item.author.avatar_url}/>
                                        
                                        <h1>{item.title}</h1>
                                        
                                        
                                        <div dangerouslySetInnerHTML={{__html:item.content}}></div>
                                    </div>
                                    
                                }
                            </div>
                        ) 
                    }
                })
                }   
            </div>
        )
    }
}





